@use "~admin-stylesheets/colors";

.nab-css-editor-sidebar__visual-editor {

	.nab-tools-panel > div:not(:first-of-type) {
		display: grid;
		grid-template-columns: repeat(2, minmax(0, 1fr));
		gap: calc(16px);
		grid-column: 1 / -1;
	}

	.nab-selector-panel {
		position: relative;

		&__menu {
			position: absolute;
			right: 10px;
			top: 6px;
		}

		&__selector {
			width: 100%;
			grid-column: 1 / -1;
		}

		&__selector-label {
			margin: 0;
			display: flex;
			gap: 5px;
			align-items: center;

			span {
				flex: 1;
				white-space: nowrap;
				overflow: hidden;
				text-overflow: ellipsis;
				min-width: 0;
			}
		}
	}

	.color-block-support-panel__inner-wrapper {
		row-gap: 0 !important;

		> div:first-child {
			margin-top: 0;
		}

		.block-editor-tools-panel-color-gradient-settings__dropdown {
			position: relative;

			.block-editor-panel-color-gradient-settings__reset {
				margin: auto 8px;
				position: absolute;
				right: 0;
				top: 8px;
			}
		}
	}

	.component-color-indicator {
		background-size: cover !important;
	}

	.nab-components-position-control {
		display: flex;
		flex-direction: column;
		gap: 2em;
	}

	.nab-components-cursor-control,
	.nab-components-floating-control,
	.nab-components-filter-control,
	.nab-components-transformations-control {
		display: flex;
		flex-direction: column;
		gap: 1em;
	}

	.nab-components-flex-control,
	.nab-components-grid-control {
		border: 1px solid
			var(--wp-components-color-gray-400, colors.$color__border-light);
		margin-top: 2em;
		padding: 1em;
		position: relative;

		&__title {
			position: absolute;
			top: 0;
			z-index: 1;
			left: 50%;
			transform: translate(-50%, -50%);
			background: colors.$color__background-editor;
			border-radius: 10px;
			border: 1px solid
				var(--wp-components-color-gray-400, colors.$color__border-light);
			padding: 1px 5px;
			text-transform: uppercase;
			font-size: 11px;
			font-weight: 500;
			color: rgb(30, 30, 30);
			line-height: 1.4;
		}

		&__wrapper {
			display: flex;
			flex-direction: column;
			gap: 1em;
		}
	}

	button.nab-components--disabled {
		background: var(--wp-components-color-gray-100, #f0f0f0) !important;
		border: none !important;
	}

	.nab-components--disabled {

		button:not(.components-toggle-group-control-option-base) {
			background: var(--wp-components-color-gray-100, #f0f0f0);
			color: var(--wp-components-color-gray-600, #949494);
		}

		.components-input-control__backdrop {
			border-color: var(
				--wp-components-color-gray-400,
				colors.$color__border-light
			) !important;
		}

		.components-unit-control__select {
			color: var(--wp-components-color-gray-600, #949494) !important;
			border-color: var(
				--wp-components-color-gray-400,
				colors.$color__border-light
			) !important;
		}

		.components-input-control__container {
			background: var(--wp-components-color-gray-100, #f0f0f0);
			color: var(--wp-components-color-gray-600, #949494);

			.components-input-control__prefix {

				button[disabled] {
					background: var(
						--wp-components-color-gray-100,
						#f0f0f0
					) !important;
					border: none !important;
				}

				button:not([disabled]) {
					background: #fff;
				}
			}
		}

		.components-toggle-group-control {
			border-color: var(
				--wp-components-color-gray-400,
				colors.$color__border-light
			) !important;

			.components-toggle-group-control-option-base[aria-checked="true"] {
				background-color: var(
					--wp-components-color-gray-600,
					#949494
				) !important;
				cursor: auto;
			}
		}

		.components-unit-control__select:hover {
			color: var(--wp-components-color-gray-600, #949494) !important;
			box-shadow: none !important;
		}

		.components-range-control__thumb-wrapper > span {
			background-color: var(
				--wp-components-color-gray-600,
				#949494
			) !important;
		}

		.components-input-control__input {
			color: var(--wp-components-color-gray-600, #949494) !important;
		}
	}

	&--empty {
		text-align: center;
		padding: 3em;

		svg {
			fill: colors.$color__fill-dark;
		}
	}
}

.nab-selector-panel__selector-dialog {

	&.components-modal__frame {
		max-width: 50em;
		width: 100%;
	}

	&__actions {
		margin-top: 2em;
		text-align: right;
	}

	&__confirm-action.components-button {
		margin-left: 0.5em;
	}
}

.modal-open .media-modal {
	z-index: 9999999;
}

.nab-components--disabled {

	.components-color-palette__custom-color-button,
	.components-toggle-group-control-option-base:not([aria-checked]) {
		cursor: auto;
	}

	.components-toggle-group-control-option-base:not([aria-checked]) {
		color: var(--wp-components-color-gray-600, #949494) !important;
	}
}

.spacing-sizes-control .components-range-control__marks :first-child {
	display: block;
}
