@use "~admin-stylesheets/colors";

.nab-css-editor-sidebar__content-editor {
	height: 100%;
	overflow-y: auto;
	width: 100%;

	.nab-content-panel {

		&__content {
			width: 100%;
			grid-column: 1 / -1;

			.ql-toolbar:has(.ql-formats--empty) {
				border-bottom: 0;
				padding: 0;
			}

			.ql-formats {
				display: flex;
				gap: 3px;

				button {
					border-radius: 3px;
					height: 24px;
					padding: 3px;
					width: 24px;

					svg .ql-stroke,
					&:hover svg .ql-stroke {
						stroke: currentcolor !important;
					}

					svg .ql-fill,
					&:hover svg .ql-fill {
						fill: currentcolor !important;
					}

					&.ql-active {
						background: #000;
						color: #fff;
					}
				}
			}

			.ql-editor > ol,
			.ql-editor > ul {
				padding-left: 0;
			}

			.ql-tooltip {
				position: initial;
				width: calc(100% + 2px);
				box-shadow: none;
				margin-left: -1px;

				input[type="text"] {
					width: auto;
					min-width: 0;
				}

				&:not(.ql-hidden) {
					display: flex;
				}
			}

			&:has(.ql-tooltip:not(.ql-hidden)) {
				margin-bottom: 3.6em;
			}

			.nab-ql-editor--has-ol-list .ql-editor {
				padding-left: 1.5em;

				> p {
					list-style: decimal;
					display: list-item;
				}
			}

			.nab-ql-editor--has-ul-list .ql-editor {
				padding-left: 1.5em;

				> p {
					list-style: disc;
					display: list-item;
				}
			}

			.nab-ql-editor--is-bold .ql-editor > p {
				font-weight: 700;
			}

			.nab-ql-editor--is-italic .ql-editor > p {
				font-style: italic;
			}

			.nab-ql-editor--is-underline .ql-editor > p {
				text-decoration: underline;
			}

			&__missing-element-warning {
				color: colors.$color__text-red;
				display: flex;
				gap: 0.5em;
				margin-bottom: 1.5em;
			}

			&__media-library-button {
				text-align: right;
				margin-top: 2em;
			}
		}
	}

	&--empty {
		text-align: center;
		padding: 3em;

		svg {
			fill: colors.$color__fill-dark;
		}
	}
}
